
.CardFront{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    color: #FFFFFF;
    padding: 1.1875rem;
    min-width: 17.875rem;
    min-height: 9.8125rem;
    border-radius: 1rem;
    z-index: 1;
    img{
        height: 1.875rem;
        margin-bottom: 2rem;
    }
    .CardNumber{
        font-size: 18px;
        letter-spacing: 2.2px;
        min-width: 14.9375rem;
        margin-bottom: 1rem;
    }
    .holder-expdate{
        min-width: 15.3195rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 0.5625rem;
        line-height: 0.6875rem;
        letter-spacing: 0.0804rem;
        text-transform: uppercase;
    }
}